</template>

<script>
  import websiteTop from './websiteTop'
  import websiteBottom from './websiteBottom'
  import topHeader from './topHeader'
  export default {
    name: "approval",
    components:{
      websiteBottom,
      websiteTop,
      topHeader
    },
    data(){
      return{
        navShow:{
          index:false,
          admin:true
        }
      }
    },

    computed:{
      
    },
    mounted(){
      console.log(this.$route)
    }
  }
</script>

<style scoped>
  .admin{
    width: 1200px;
    margin: 0 auto;
  }
  .admin:after{display:block; content:"clear"; height:0; clear:both; overflow:hidden; visibility:hidden;}
  .admin .nav{
    width:200px;
    height:291px;
    background-color:rgba(255,255,255,1);
    float: left;
  }
  .admin .content{
    float: right;
    width: 990px;
    min-height:800px;
    margin-left: 10px;
    background-color: #ffffff;
  }
  #bac{
    background-color: #f5f5f5;
  }


</style>



