<template>
  <div id="bac">
    <top-header></top-header>
    <website-top :navShow="navShow"></website-top>
      <div class="admin">

          <div class="nav">
            <el-menu :default-active="$route.path" class="el-menu-vertical-demo" :router="true">
              <el-menu-item index="/admin/adminIndex">
                <i class="el-icon-menu"></i>
                <span slot="title">工作台</span>
              </el-menu-item>
              <el-menu-item index="/admin/adminMytask">
                <i class="el-icon-menu"></i>
                <span slot="title">我的任务</span>
              </el-menu-item>
              <el-menu-item index="/admin/adminLaunch">
                <i class="el-icon-document"></i>
                <span slot="title">项目发起</span>
              </el-menu-item>
              <el-submenu index="2">
                <template slot="title">
                  <i class="el-icon-location"></i>
                  <span slot="title">审批任务</span>
                </template>
                <el-menu-item-group>
                  <el-menu-item index="/admin/adminExamine">待审批</el-menu-item>
                  <el-menu-item index="/admin/adminInspected">任务列表</el-menu-item>
                </el-menu-item-group>
              </el-submenu>




            </el-menu>

          </div>
          <div class="content">
            <router-view></router-view>
          </div>

      </div>
    <!--<website-bottom></website-bottom>-->
  </div>
</template>

<script>
  import websiteTop from './websiteTop'
  import websiteBottom from './websiteBottom'
  import topHeader from './topHeader'


    export default {
        name: "admin",
      components:{
        websiteBottom,
        websiteTop,
        topHeader
      },
      data(){
          return{
            navShow:{
              index:true,
              admin:false
            }
          }
      }
    }
</script>


<style scoped>
  .admin{
    width: 1200px;
    margin: 0 auto;
  }
  .admin:after{display:block; content:"clear"; height:0; clear:both; overflow:hidden; visibility:hidden;}
  .admin .nav{
    width:200px;
    height:340px;
    background-color:rgba(255,255,255,1);
    float: left;
  }
  .admin .content{
    float: right;
    width: 990px;
    min-height:800px;
    margin-left: 10px;
    background-color: #ffffff;
  }
  #bac{
    background-color: #f5f5f5;
  }

</style>





