import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import index from '@/components/index'
import admin from '@/components/admin'
import adminIndex from '@/components/adminIndex'
import adminExamine from '@/components/adminExamine'
import adminInspected from '@/components/adminInspected'
import adminLaunch from '@/components/adminLaunch'
import approval from '@/components/approval'
import approvalIndex from '@/components/approvalIndex'
import approvalAcceptance from '@/components/approvalAcceptance'
import adminMytask from '@/components/adminMytask'
import { resolve } from 'url';
import {getToken} from '@/util/user.js'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path:'/index',
      component: index
    },
    {
      path:'/admin',
      component: admin,
      children:[
        {
          path:'adminIndex',
          component:adminIndex
        },
        {
          path:'adminExamine',
          component:adminExamine
        },
        {
          path:'adminInspected',
          component:adminInspected
        },
        {
          path:'adminLaunch',
          component:adminLaunch
        },
        {
          path:'adminMytask',
          component:adminMytask
        },
        {
          path:'news',
          name:'news',
          component:resolve=>require(['@/components/news.vue'], resolve)
        },
        {
          path:'problem',
          name:'problem',
          component:resolve=>require(['@/components/problem.vue'], resolve)
        }
      ]
    },
    {
      path:'/approval',
      component:approval,
      children:[
        {
          path:'approvalIndex',
          component:approvalIndex
        },
        {
          path:'approvalAcceptance',
          component:approvalAcceptance
        },
        {
          path:'adminList',
          component:resolve=>require(['@/components/adminList.vue'], resolve),
          meta:{title:'验收列表'}
        }

      ]

    }
  ]
})


// 路由拦截 可在mate设置属性是否需要登陆
  router.beforeEach((to,form,next)=>{
    // console.log((to.matched.some(route => route.meta.requiresAuth))
    if(to.matched.some(route => route.meta.requiresAuth)){
      if(getToken()!==null){
        next()
      }else{
        if(to.name=='login'){
          next()
          return
        }
      }
    }else{
      next()
    }
  })

export default router



