<template>
  <div class="login">
    <website-top :navShow="navShow"></website-top>
    <div class="bacImg">
      <div class="loginInfo">
        <div class="box">
          <input placeholder="请输入手机号">
          <input placeholder="请输入密码" :type="passwordImg.inputType">
          <img :src="passwordImg.img" @click="changSee" class="see">
          <h4>忘记密码</h4>
          <div class="btn" @click="toLogin">登录</div>

        </div>

      </div>
    </div>
    <website-bottom></website-bottom>
  </div>

</template>

<script>
  import websiteTop from './websiteTop'
  import websiteBottom from './websiteBottom'
    export default {
      name: "login",
      components:{
        websiteTop,
        websiteBottom
      },
      data(){
        return{
          passwordImg:{img:require('./../assets/img/loginHidden.png'),inputType:'password'},
          navShow:{
              index:false,
              admin:false
            }
        }
      },
      methods:{
        changSee(){
          this.passwordImg.img = this.passwordImg.img === require('./../assets/img/loginHidden.png')?require('./../assets/img/loginSee.png'):require('./../assets/img/loginHidden.png')
          this.passwordImg.inputType = this.passwordImg.inputType === 'password'?'text':'password'
        },
        toLogin(){
          var myreg=/^[1][3,4,5,7,8][0-9]{9}$/;

          this.$router.push('/index')
        }
      }
    }
</script>

<style scoped>

  .login .bacImg{
    width: 100%;
    height: 800px;
    background-image: url("./../assets/img/loginBac.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
  .login .bacImg .loginInfo{
    width: 1200px;
    height: 800px;
    margin: 0 auto;
  }
  .login .bacImg .loginInfo .box{
    width:500px;
    height:395px;
    background:rgba(255,255,255,1);
    border-radius:4px;
    float: right;
    margin-right: 23px;
    margin-top: 165px;
    box-sizing: border-box;
    padding-top: 22px;
    position: relative;
  }
  .login .bacImg .loginInfo .box input{
    width:450px;
    height:60px;
    background:rgba(255,255,255,0);
    border:1px solid rgba(204,204,204,1);
    border-radius:4px;
    box-sizing: border-box;
    padding-left: 65px;
    margin-left: 27px;
    margin-top: 40px;
    background-repeat: no-repeat;
    background-size: 30px 30px;
    background-position: 20px;
  }
  .login .bacImg .loginInfo .box input:nth-child(1){
    background-image: url("./../assets/img/loginAccount.png");
  }
  .login .bacImg .loginInfo .box input:nth-child(2){
    background-image: url("./../assets/img/loginPassword.png");

  }
  .login .bacImg .loginInfo .box img{
    position: absolute;
    left: 429px;
    top: 178px;
  }
  .login .bacImg .loginInfo .box h4{
    width: 100%;
    height: 50px;
    line-height: 50px;
    box-sizing: border-box;
    padding-right: 23px;
    text-align: right;
    font-size:16px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(51,51,51,1);
    background-image: url("./../assets/img/loginQuestion.png");
    background-repeat: no-repeat;
    background-size: 20px 20px;
    background-position: 386px;
  }
  .login .bacImg .loginInfo .box .btn{
    width:450px;
    height:60px;
    background:rgba(37,54,145,1);
    border-radius:4px;
    margin: 10px auto 0 auto;
    font-size:24px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(255,255,255,1);
    text-align: center;
    line-height: 60px;
  }

</style>



