<template>
  <div class="topHeader">
    <h5 class="h5" @click="toLogin" v-if="user">登录</h5>
    <el-dropdown class="h5 user" trigger="click" v-else>
      <span class="el-dropdown-link">ID12345
        <i class="el-icon-caret-bottom el-icon--right"></i>
      </span>

        <el-dropdown-menu slot="dropdown" @click="clear">
        <el-dropdown-item class="clearfix">
          <span @click='clear'>退出</span>
        </el-dropdown-item>
        <el-dropdown-item class="clearfix">
          <router-link to="/admin/adminIndex" tag="span">发起人</router-link>
        </el-dropdown-item>
         <el-dropdown-item class="clearfix">
          <router-link to="/approval/approvalIndex" tag="span">审核人</router-link>
        </el-dropdown-item>
      </el-dropdown-menu>


    </el-dropdown>

    <router-link to="/admin/problem">
      <h6>常见问题</h6>
    </router-link>

    <router-link to="/admin/news">
      <span>
        <i class="el-icon-bell"></i>
        消息
      </span>
    </router-link>

  </div>
</template>

<script>
export default {
  name: "topHeader",
  data(){
    return{
      user:false
    }
  },
  methods: {
    toLogin() {
      this.$router.push("/login");
    },
    clear(){
      console.log('退出')
      this.user=true;
      // this.$router.push("/login");
    }
  }
};
</script>

<style scoped lang='scss'>
.topHeader {
  width: 1200px;
  height: 40px;
  line-height: 40px;
  margin: 0 auto;
  a {
    color: rgba(51, 51, 51, 1);
  }
}
.topHeader span {
  float: right;
  font-size: 16px;
  display: block;
  line-height: 42px;
}
.topHeader h6 {
  font-size: 16px;
  font-family: PingFang-SC-Medium;
  font-weight: 500;
  color: rgba(51, 51, 51, 1);
  float: right;
  margin-left: 32px;
}
.topHeader .h5 {
  font-size: 16px;
  font-family: PingFang-SC-Medium;
  font-weight: 500;
  color: rgba(37, 54, 145, 1);
  float: right;
  margin-left: 32px;
  cursor: pointer;
}
.topHeader .user{
   font-size:16px;
   font-family:PingFang-SC-Medium;
font-weight:500;
color:rgba(37,54,145,1);
   }
</style>



