<template>
  <div class="index">
    <h3>欢迎你，法制科同事，王张丽！</h3>
    <ul class="info">
      <li>
        <h2>5</h2>
        <h4>审核未通过项目</h4>
      </li>
      <li>
        <h2>2</h2>
        <h4>验收未通过项目</h4>
      </li>
      <li>
        <h2>10</h2>
        <h4>审核通过项目</h4>
      </li>
      <li>
        <h2>15</h2>
        <h4>验收通过项目</h4>
      </li>
    </ul>
    <el-table
      :data="tableData2"
      style="width: 100%;margin-top: 20px"
      :row-class-name="tableRowClassName"
      header-cell-class-name="tableHead"
      cell-class-name="tableCell"
    >
      <el-table-column type="index" label="序号" width="100px"></el-table-column>
      <el-table-column prop="address" label="任务标题"></el-table-column>
      <el-table-column prop="name" label="审批进度"></el-table-column>
      <el-table-column prop="date" label="时间"></el-table-column>
      <el-table-column  label="操作">
        <template slot-scope="scope">
          <el-button type="primary" plain>查看</el-button>
          <el-button type="danger" plain @click="del(scope.row,scope.$index)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      background
      layout="prev, pager, next"
      :total="1000"
      style="text-align: center;margin-top: 20px;"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: "approvalIndex",
  data() {
    return {
      tableData2: [
        {
          date: "2016-05-02",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-04",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-01",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        }
      ]
    };
  },
  methods: {
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "";
      } else {
        return "cellBlue";
      }
    },
    // 删除按钮
    del(row,index){
      
          this.$confirm('此操作将删除该记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.tableData2.splice(index,1)
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      
    }
  }
};
</script>

<style scoped>
.index h3 {
  font-size: 16px;
  font-family: PingFang-SC-Medium;
  font-weight: 500;
  color: rgba(37, 54, 145, 1);
  box-sizing: border-box;
  padding-top: 40px;
  padding-left: 21px;
  padding-bottom: 19px;
}
.index .info {
  width: 980px;
  height: 120px;
  margin: 0 auto;
}
.index .info li {
  float: left;
  width: 245px;
  height: 120px;
  text-align: center;
  box-sizing: border-box;
  padding-top: 28px;
}
.index .info li h2 {
  font-size: 40px;
  font-family: PingFang-SC-Bold;
  font-weight: bold;
  color: rgba(37, 54, 145, 1);
}
.index .info li h4 {
  font-size: 16px;
  font-family: PingFang-SC-Medium;
  font-weight: 500;
  color: rgba(51, 51, 51, 1);
  margin-top: 18px;
}
</style>
<style>
.el-table .tableHead {
  text-align: center;
  font-size: 16px;
  font-family: PingFang-SC-Medium;
  font-weight: 500;
  color: rgba(51, 51, 51, 1);
  background-color: rgba(245, 247, 255, 1);
}
.el-table .tableCell {
  text-align: center;
  font-size: 16px;
  font-family: PingFang-SC-Medium;
  font-weight: 500;
  color: rgba(51, 51, 51, 1);
}
.el-table .cellBlue {
  background-color: rgba(245, 247, 255, 1);
}
.el-pagination {
  margin-top: 40px;
  margin-bottom: 100px;
}
</style>



